import "./styles.scss";

document.getElementById("app").innerHTML = `<p>Click ðŸ‘† this button</p>`;

document.getElementById("load-btn").addEventListener("click", () => {
  document.getElementById("app").innerHTML = "waiting...";

  fetch("https://gp-js-test.herokuapp.com/pizza")
    .then((response) => response.json())
    .then((data) => {
      document.getElementById("pizza").innerHTML = "";
      let count = data.party.filter((x) => x.eatsPizza === true).length;
      let ul = document.createElement("ul");
      document.getElementById("app").innerHTML = `<p>Click ðŸ‘† this button</p>`;
      for (let i = 0; i < count; i++) {
        let li = document.createElement("li");
        li.innerHTML = data.party[i].name;
        ul.appendChild(li);
      }
      document.getElementById("app").appendChild(ul);

      let circle = document.createElement("div");
      document.getElementById("pizza").appendChild(circle);
      circle.className = "circle";

      const cutPizza = (numberOfPersons) => {
        let arr = [];
        for (let i = 0; i < numberOfPersons / 2; i++) {
          arr[i] = document.createElement("div");
          arr[i].className = "vl";
          arr[i].id = `${i}`;
          circle.appendChild(arr[i]);
          document.getElementById(arr[i].id).style.transform = `rotate(${
            (360 / numberOfPersons) * i
          }deg)`;
        }
      };
      cutPizza(count);
    });
});
